.footer {
    display: flex;
    flex-direction: column;
    justify-content: stretch;

    background-color: #e8e6e6;
    margin-top: 20px;
    padding-bottom: 20px;
}

.footer .legals-links,
.footer .copyright {
    margin-top: 10px;
    text-align: center;
}

.footer .legals-links {
    line-height: 30px;
}

.footer .legals-links a {
    text-decoration: none;
    color: #000;
}

.footer .newsletter {
    margin: 0 20px 0 20px;
}