.header {
    /* background-color: lightgreen; */

    display: flex;
    flex-direction: column;
    justify-content: stretch;
}

.header {
    border-bottom: 1px solid #000;
}

.app-menu {
    /* background-color: lightsalmon; */

    display: flex;
    container: flex;
    flex-direction: row;
    align-items: center;
}

.app-menu {
    border-bottom: 1px solid #000;
    height: 50px;
    padding: 0 20px 0 20px;
}

.app-menu .socials-links {
    flex-grow: 1;
}

.app-menu .menu-items {
    /* background-color: lightyellow; */

    flex-shrink: 1;
    text-align: center;
}

.header .reason p {
    /* background-color: lightpink; */

    max-width: 70%;
    margin: auto;

    font-size: 15px;
    text-align: center;
    line-height: 20px;
    letter-spacing: .4rem;
}

.header .title p {
    /* background-color: lightgoldenrodyellow; */
    
    max-width: 90%;
    margin: auto;

    font-size: 40px;
    text-align: center;
}

.drawer-menu {
    /* background-color: lightblue; */

    display: flex;
    flex-direction: column;
    justify-content: stretch;

    align-items: stretch;
    align-content: stretch;
}

.drawer-menu-content {
    height: 100%;
    display: flex;
    flex-direction: column;
    
    /* align-items: stretch; */
}

.close-button {
    height: 15vh;
    /* background-color: lightcyan; */
    text-align: right;
    padding: 10px;
}

.menu-list {
    height: 85vh;
    /* background-color: lightpink; */

    display: flex;
    flex-direction: column;
}

.menu-list a {
    padding: 25px 0 25px;
    text-align: center;

    font-size: 1.2rem;
    text-decoration: none;
    color: #000;
}

.menu-list a:active {
    color: #6600ea;
}