.publications {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
}

.publications-loading {
    padding: 20px;
    text-align: center;
}

.markdown-content {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
 
    padding: 0 10px 0 10px;
}

.contact-us {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    
    padding: 0 10px 0 10px;
    text-align: center;
}

.contact-us > form {
    display: flex;
    flex-direction: column;
    justify-content: stretch;

    row-gap: 10px;
}

